import Swiper from 'swiper';
import { Navigation, Pagination, EffectCreative } from 'swiper/modules';

export const fleetSlider = () => {
  const sliderNav = new Swiper(".fleet__slider-nav", {
    loop: true,
    spaceBetween: 0,
    slidesPerView: 1,
    freeMode: true,
    watchSlidesProgress: true,
    breakpoints: {
      768: {
        slidesPerView: 3,
        spaceBetween: 16,
      },
      1279: {
        slidesPerView: 5,
        spaceBetween: 16,
      },
    },
  });

  new Swiper('.fleet__slider', {
    modules: [Navigation, Pagination, EffectCreative],
    direction: 'horizontal',
    loop: true,
    centeredSlides: true,
    slidesPerView: 'auto',
    pagination: {
      el: ".swiper-pagination",
      type: "fraction",
    },
    navigation: {
      nextEl: '.fleet__btn-prev',
      prevEl: '.fleet__btn-next',
    },
    effect: "creative",
    creativeEffect: {
      limitProgress: 2,
      prev: {
        translate: ['50%', 0, 0],
        scale: [0.3],
      },
      next: {
        translate: ['-50%', 0, 0],
        scale: [0.3],
      },
    },
    thumbs: {
      swiper: sliderNav,
    },
    breakpoints: {
      768: {
        slidesPerView: 3,
        creativeEffect: {
          limitProgress: 2,
          prev: {
            translate: ['150%', 0, 0],
            scale: [0.5],
          },
          next: {
            translate: ['-150%', 0, 0],
            scale: [0.5],
          },
        },
      },
    },
  });
}
